import { createLibp2p } from 'libp2p';
import { webSockets } from '@libp2p/websockets';
import { noise } from '@chainsafe/libp2p-noise';
import { mplex } from '@libp2p/mplex';
import { kadDHT } from '@libp2p/kad-dht';
import { identify } from '@libp2p/identify';
import { pipe } from 'it-pipe';
import { fromString, toString } from 'uint8arrays';

// Create the libp2p node
const node = await createLibp2p({
  transports: [webSockets()],
  connectionEncryption: [noise()],
  streamMuxers: [mplex()],
  services: {
    dht: kadDHT(),
    identify: identify(),  // Include identify service
  }
});


// Start the libp2p node
await node.start();
console.log('Libp2p node started');
console.log('DHT service:', node.services.dht);

// Correct method to listen for connection events
node.addEventListener('peer:connect', (evt) => {
  const peerId = evt.detail;
  console.log('Connected to:', peerId.toString());
});

node.addEventListener('peer:disconnect', (evt) => {
  const peerId = evt.detail;
  console.log('Disconnected from:', peerId.toString());
});

// Periodically log routing table to check peer discovery
setInterval(() => {
  console.log('Routing Table Peers:', node.services.dht.routingTable.peers);
}, 5000);

// Check if both nodes are connected to the same DHT
const checkDHTConnection = () => {
  console.log('DHT Routing Table:', node.services.dht.routingTable);
  console.log('Connected peers:', node.getPeers());
};

setInterval(checkDHTConnection, 10000); // Log every 10 seconds

const advertiseRoom = async (roomId) => {
  console.log('DHT Network:', node.services.dht.network);
  const roomKey = `/rooms/${roomId}`;
  console.log('Your node peer ID:', node.peerId.toString());
  await node.services.dht.provide(roomKey);
  console.log(`Room ${roomId} advertised with key ${roomKey}`);
  console.log('Room advertised: Checking provider status...');

  // Debugging: Check closest peers in the DHT
  try {
    const closestPeers = await node.services.dht.getClosestPeers(roomKey);
    console.log('Closest peers:', closestPeers);
  } catch (error) {
    console.error('Error finding closest peers:', error);
  }
};



// Room Join: Find peers in the room
const joinRoom = async (roomId) => {
  console.log('DHT Network:', node.services.dht.network);
  const roomKey = `/rooms/${roomId}`;
  console.log('Your node peer ID:', node.peerId.toString());
  console.log(`Searching for providers of ${roomKey}...`);
  
  await new Promise(resolve => setTimeout(() => console.log('Hello'), 1000)); // This is correct
    
  try {
    const peers = await node.services.dht.findProviders(roomKey);
    if (!Array.isArray(peers) || peers.length === 0) {
      console.error(`No peers found for room ${roomId}`);
      return [];
    }
    
    console.log(`Found ${peers.length} peers for room ${roomId}`);
    peers.forEach(peer => {
      console.log(`Discovered peer: ${peer.id.toString()}`);
    });
    
    return peers; // Return discovered peers
  } catch (error) {
    console.error('Error during peer discovery:', error);
    return [];
  }
};

// Messaging Handler: Handle incoming chat messages
const handleChat = async ({ stream }) => {
  await pipe(
    stream.source,
    async function (source) {
      for await (const msg of source) {
        console.log(`Received message: ${toString(msg)}`);
        const chatLog = document.getElementById('chatLog');
        const li = document.createElement('li');
        li.textContent = `Peer: ${toString(msg)}`;
        chatLog.appendChild(li);
      }
    }
  );
};

// Register the message handler
node.handle('/chat/1.0.0', handleChat);

// Send messages to a specific peer
const sendMessageToPeer = async (peerId, message) => {
  const { stream } = await node.dialProtocol(peerId, '/chat/1.0.0');
  await pipe([fromString(message)], stream.sink);
  console.log(`Sent message to ${peerId}`);
};

// Example function to join room and send a message
const joinAndSendMessage = async (roomId, message) => {
  const peers = await joinRoom(roomId);
  if (peers.length > 0) {
    await sendMessageToPeer(peers[0].id, message);
  }
};

// Expose the functions to the window for testing
window.createRoom = (roomId) => {
  console.log('Room ID for Create Room:', roomId);  // Log roomId
  advertiseRoom(roomId);  // Your existing function
};

window.joinRoom = (roomId) => {
  console.log('Room ID for Join Room:', roomId);  // Log roomId
  joinRoom(roomId);  // Your existing function
};

window.sendMessage = (roomId, message) => {
  console.log('Room ID for Send Message:', roomId);  // Log roomId
  console.log('Message:', message);  // Log the message
  joinAndSendMessage(roomId, message);  // Your existing function
};